      // 
      this.operaType = val;
      // 
      this.dialogVisible = true;
      // 
      if (val === 3) {
        // 
        this.getTypeData(this.ztreeSelectedNode.id);
        this.titlename = BTN_TEXT.MODIFY;
        this.showModelItem = false;
      } else if (val === 1) {
        this.titlename = BTN_TEXT.ADD_BORTHER;
        // 
        if (this.ztreeSelectedNode.pId > 0) {
          this.showModelItem = true;
        } else {
          this.showModelItem = false;
        }
      } else if (val === 2) {
        this.titlename = BTN_TEXT.ADD_CHILDREN;
        // 
        if (
          this.ztreeSelectedNode.id !==
          '-1'
        ) {
          this.showModelItem = true;
        } else {
          this.showModelItem = false;
        }
      }
